import React from "react";
import useAlert from "../common/hooks/useAlert";
import useFreighterAPI from "../common/hooks/useFreighterAPI";
import Button from "../components/button";
import FormField from "../components/formField";
import { Link, useNavigate } from "react-router-dom";

const checkFormValidation = (form: any): string => {
    const { email, password } = form;
    if (!email) return "Email Address is required";
    if (!password) return "Password is required";
    return "";
};

const Login = () => {

    const navigate = useNavigate();
    const [spinner, setSpinner] = React.useState(false);
    const [state, setState] = React.useState({
        email: "",
        password: ""
    });

    const { showAlert } = useAlert();

    // Send Object to API
    const login = () => {

        const validationError = checkFormValidation(state);
        if (validationError) {
            showAlert({ type: "error", message: validationError, duration: 2000 });
            return;
        }

        try {
            setSpinner(true);
            fetch('https://lockb0x-api-dev.azurewebsites.net/api/User/Token', {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                    'Access-Control-Allow-Origin': '*'
                },
                body: JSON.stringify({ ...state })
            })
                .then((response) => response.json())
                .then((data) => {
                    setSpinner(false);
                    if (data.status == 'NOK') {
                        showAlert({ type: "error", message: data.errorMessage, duration: 2000 });
                    }

                    if (data.status == 'OK') {
                        navigate('/dashboard')
                    }

                }).
                catch(function (error) {
                    setSpinner(false);
                    showAlert({ type: "error", message: error, duration: 2000 });
                });

        } catch (e: any) {
            setSpinner(false);
            console.log(e);
            showAlert({ type: "error", message: e.message, duration: 2000 });
        }
    };


    return (
        <div className="items-center w-96 mx-auto justify-center mt-20">
            <div className="h-24 mb-10">
                <img src='logotitle.png' alt="Logo" />
            </div>
            <form >
                <div className="shadow sm:rounded-md sm:overflow-hidden">
                    <div className="px-4 py-5 bg-white space-y-6 sm:p-6">
                        <div className="grid grid-cols-3 gap-6">
                            <div className="col-span-6 sm:col-span-3">
                                <FormField
                                    isMandatory
                                    label={"Email Address"}
                                    value={state.email}
                                    onChangeText={(email) => setState({ ...state, email })}
                                    inputType={"email"}
                                />
                            </div>
                            <div className="col-span-6 sm:col-span-3">
                                <FormField
                                    isMandatory
                                    label={"Password"}
                                    value={state.password}
                                    onChangeText={(password) => setState({ ...state, password })}
                                    inputType={"password"}
                                />
                            </div>
                        </div>
                        <div className="flex justify-center ">
                            <Button
                                label={"Login"}
                                loading={spinner}
                                onPress={login}
                                size="lg"
                                disabled={spinner}
                            />
                        </div>
                    </div>
                    <div className="px-4 py-3 bg-gray-200 flex flex-col justify-center items-center sm:px-6">
                        <Link style={{ textDecoration: 'underline' }} to='/'>Create Account</Link>
                        <p>The Firm LLC Copyright 2022</p>
                    </div>
                </div>
            </form>
        </div>
    );
};

export default Login;
